/* eslint react/prop-types: 0 */
import * as React from "react";
import CodeBlock from "../components/codeBlock";
import Author from "./../components/post/author";
// import Title from "./../components/title"
// import ReactDom from 'react-dom'

const footnoteRegex = /\[\^[a-zA-Z0-9]+\]/g;
const footnoteATagsRegex = /\[\^[a-zA-Z0-9]+\]\:/g;

const replaceFootnoteToAnchor = node => {
  if (Array.isArray(node)) {
    return node.map(item => replaceFootnoteToAnchor(item));
  } else if (typeof node === "object") {
    return node;
  } else if (typeof node === "string") {
    const matchList = node.match(footnoteRegex);
    const matchAList = node.match(footnoteATagsRegex);
    if (matchAList) {
      return matchAList
        .map(item => item.replace(/[\[\^\:\]]/g, ""))
        .map(item => {
          return (
            <span id={item}>
              <br /> {item} {node.split(footnoteATagsRegex)[1]}
            </span>
          );
        });
    }
    if (!matchList) {
      return node;
    }

    const matchReplaceList = matchList.map(item =>
      item.replace(/[\[\^\]]/g, "")
    );

    const footnoteTextList = node.split(/\[\^[a-zA-Z0-9]+\]/g);
    return footnoteTextList.map((item, index) => {
      return (
        <span>
          {item}
          <a
            className="ml-[1px] mr-[4px] text-[#047d65] text-[14px] font-semibold inline-block -translate-y-2 translate-x-px"
            href={`#` + matchReplaceList[index]}
          >
            {matchReplaceList[index]}
          </a>
        </span>
      );
    });
  }

  return node;
};

const components = {
  wrapper: ({ components, ...rest }) => {
    return <main {...rest} className="flex flex-col scroll-smooth	" />;
  },

  h1: props => (
    <h1
      {...props}
      className={"text-[32px] font-bold text-[#3A3B40] mt-2 mb-4"}
    />
  ),
  h2: props => (
    <h2
      {...props}
      className={"text-[1.75rem] font-bold text-[#3A3B40] mt-6 mb-4"}
    />
  ),
  h3: props => (
    <h3
      {...props}
      children={replaceFootnoteToAnchor(props.children)}
      className={"text-[1.5rem] font-bold text-[#3A3B40] mt-6 mb-2"}
    />
  ),
  h4: props => (
    <h4
      {...props}
      className={"text-[1.25rem] font-bold text-[#3A3B40] mt-4 mb-4"}
    />
  ),
  h5: props => (
    <h5
      {...props}
      className={"text-[24px] font-bold text-[#3A3B40] mt-2 mb-4"}
    />
  ),
  h6: props => (
    <h5
      {...props}
      className={"text-[22px] font-bold text-[#3A3B40] mt-2 mb-4"}
    />
  ),
  p: props => (
    <p
      {...props}
      children={replaceFootnoteToAnchor(props.children)}
      className="text-[18px] my-[1rem] font-normal text-[#65666B] leading-[180%]"
    />
  ),

  a: props => {
    return <a {...props} style={{ color: "#3ca29c", fontWeight: 500 }} />;
  },
  code: props => <CodeBlock props={props} />,
  blockquote: props => (
    <blockquote
      {...props}
      className="overflow-x-auto border-l pl-5 m-0 italic"
      style={{
        display: "block",
        marginBlockStart: "0.5em",
        marginBlockEnd: "0.5em",
        marginInlineStart: "10px",
        marginInlineEnd: "10px",
        borderLeft: "6px solid rgb(59, 189, 90)",
      }}
    ></blockquote>
  ),
  ul: props => <ul {...props} className="pl-[40px] my-[16px]  list-disc "></ul>,
  li: props => {
    let liStyle = "my-1 text-[18px] text-[#65666B] leading-[180%] ";

    return <li {...props} className={liStyle}></li>;
  },
  ol: props => (
    <ol
      className="list-inside list-decimal pl-[40px] my-[16px]"
      {...props}
    ></ol>
  ),
  Author: props => {
    return (
      <Author
        email={props.email}
        name={props.name}
        description={props.description}
        date={props.date}
      ></Author>
    );
  },
  table: props => (
    <table {...props} className="my-[2rem] text-[#2d3749]"></table>
  ),
  tr: props => <tr {...props} className="text-left "></tr>,
  td: props => (
    <td
      {...props}
      className="py-[4px] border-solid
      border-b-2
      border-[#e2e8f0]"
    ></td>
  ),
  th: props => (
    <th
      {...props}
      className="py-[4px] border-solid
      border-b-2
      border-[#e2e8f0]"
    ></th>
  ),
  img: props => <img {...props} className="my-10"></img>,
  hr: props => <hr {...props} className="text-[#8B8D94]"></hr>,
};

export default components;
