import React from "react";
import Highlight, { defaultProps } from "prism-react-renderer";
import { borderColor, borderRadius } from "polished";
// import theme from 'prism-react-renderer/themes/github';

const CodeBlock = ({ props }) => {
  // 언어를 찾기위한 과정
  const className = props.className || "";
  const matches = className.match(/language-(?<lang>.*)/);
  const language = matches?.groups?.lang ?? "";

  let customProps = JSON.parse(JSON.stringify(defaultProps.theme));
  customProps.plain.color = "#2e383c";
  customProps.styles = customProps.styles.map((item, index) => {
    switch (index) {
      case 0:
        return { style: { color: "#75787b" }, types: item.types };
      case 2:
        return { style: { color: "#75438a" }, types: item.types };
      case 3:
        return { style: { color: "#2d73b2" }, types: item.types };
      case 5:
        return { style: { color: "#2d8169" }, types: item.types };
      case 6:
        return { style: { color: "#047d65" }, types: item.types };
    }

    return { style: { color: "#2e383c" }, types: item.types };
  });

  const highlightDom = () => {
    return (
      <Highlight
        {...defaultProps}
        code={props.children.trim()}
        language={language}
        theme={customProps}
      >
        {({ className, style, tokens, getLineProps, getTokenProps }) => (
          <pre
            className={
              className + " overflow-x-auto my-5 font-mono font-mono	p-[20px]"
            }
            style={{
              backgroundColor: "#F7F8FA",
              border: "1px solid #DCDFE3",
              borderRadius: "16px",
              color: "#1E1E1E",
            }}
          >
            <code className={language + "font-mono "}>
              {tokens.map((line, i) => {
                return (
                  <div
                    {...getLineProps({ line, key: i })}
                    className="py-[0.2rem]"
                  >
                    <span
                      className="mr-4 ml-2 font-mono"
                      style={{ color: "#dcdee1" }}
                    >
                      {i + 1}
                    </span>
                    {line.map((token, key) => {
                      return <span {...getTokenProps({ token, key })} />;
                    })}
                  </div>
                );
              })}
            </code>
          </pre>
        )}
      </Highlight>
    );
  };

  if (language === "") {
    return (
      <span
        className="
      text-[#3d3d36] 
      text-[0.85em]
      bg-[#F7F8FA] 
      px-[5px] 
      py-[5px] 
      m-[5px] 
      rounded-md 
      border-solid
      border
      border-[#DCDFE3]
      font-medium	
      font-mono	"
      >
        {props.children}
      </span>
    );
  }
  return highlightDom();
};

export default CodeBlock;
